<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="d-flex mt-4">
      <h3 class="m-0"></h3>
      <button class="btn btn-outline-primary ml-auto" @click.prevent="openCouponModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in coupons" :key="index">
          <td class="align-middle">{{ item.title }}</td>
          <td class="align-middle">{{ item.percent }} %</td>
          <td class="align-middle">{{ item.due_date }}</td>
          <td class="align-middle">
            <span class="text-success font-weight-bold" v-if="item.is_enabled"></span>
            <span class="text-dark font-weight-bold" v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click.prevent="openCouponModal(false, item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click.prevent="opendelModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title"
                v-model="tempCoupon.title"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input type="text" class="form-control" id="coupon_code"
                v-model="tempCoupon.code"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" 
                v-model="tempCoupon.due_date"
              id="due_date">
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input type="number" class="form-control" id="price"
                v-model="tempCoupon.percent"
                placeholder="">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  v-model="tempCoupon.is_enabled"
                  :true-value="1"
                  :false-value="0"
                  id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-outline-primary" @click.prevent="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>

    <!--   Modal -->
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-dange" @click.prevent="delCoupon(tempCoupon.id)"></button>
          </div>
        </div>
      </div>
    </div>


    
  </div>
</template>

<script>
import $ from 'jquery';

  export default {
    data() {
      return {
        coupons: [],
        tempCoupon: {
          title: '',
          code: '',
          due_date: 0,
          percent: 100,
          is_enabled: 0,
        },
        isNew: false,
        isLoading: false,
      };
    },
    methods: {
      getCoupons(page = 1) {
        // 
        const vm = this;
        let api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupons?page=${page}`;
        vm.isLoading = true;
        this.$http.get(api).then((response) => {
          console.log(response.data);
          vm.isLoading = false;
          vm.coupons = response.data.coupons;
        });
      },
      openCouponModal(isNew, item) {
        //   modal
        const vm = this;
        if (isNew) {
          vm.tempCoupon = {};
          vm.isNew = true;
        } else {
          vm.tempCoupon = Object.assign({}, item);
          vm.isNew = false;
        };
        $('#couponModal').modal('show');
      },
      updateCoupon() {
        //   
        const vm = this;
        let api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon`;
        let httpmethod = 'post';
        if (vm.isNew == false) {
          api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon/${vm.tempCoupon.id}`;
          httpmethod = 'put';
        };
        this.$http[httpmethod](api, { data: vm.tempCoupon}).then((response) => {
          console.log(response.data);
          if (response.data.success) {
            $('#couponModal').modal('hide');
            vm.getCoupons();
          };
        });
      },
      opendelModal(item) {
        //   modal
        const vm = this;
        vm.tempCoupon = Object.assign({}, item);
        $('#delCouponModal').modal('show');
      },
      delCoupon(id) {
        // 
        const vm = this;
        let api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon/${id}`;
        this.$http.delete(api).then((response) => {
          console.log(response.data);
          if (response.data.success) {
            $('#delCouponModal').modal('hide');
            vm.getCoupons();
          };
        });
      },
    },
    created() {
      this.getCoupons();
    },
  };
</script>


